* {
    font-family: "Poppins", sans-serif;
    margin: 0;
    padding: 0;
}

.App {
    text-align: left;
    margin: 0 !important;
    padding: 0 !important;
}

.App-logo {
    height: 6vmin;
    pointer-events: none;
    font-size: 36px;
}
.projectHeader {
    background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.1)), url('./assets/img/bgcolorful.jpg');
    background-size: cover;
    color: #fff;
    height: 82vh !important;
}
.logo-title {
    font-weight: bolder;
    font-size: 16px;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: #fff;
}
@media(prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 18s linear;
    }
}

.App-header {
    background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.1)), url('./assets/img/bgcolorful.jpg');
    background-size: cover;
    min-height: 82vh;
    display: flex;
    letter-spacing: 1px;
    justify-content: space-evenly;
    align-items: center;
    font-size: calc(10px + 2vmin);
    color: rgb(255, 255, 255);
    font-weight: bolder;
    padding: 0 50px;
    text-align: left;
}

.scrollable {
    background-color: #343a40;
    height: 10vh;
    display: block;
    cursor: pointer;
    font-size: larger;
    font-weight: bold;
    align-items: center;
    text-align: center;
}
.scrollableProject {
    background-color: #343a40;
    height: 10vh;
    display: block;
    cursor: pointer;
    font-size: larger;
    font-weight: bold;
    align-items: center;
    text-align: center;
}

.customLink {
    font-weight: normal;
    text-transform: uppercase;
    letter-spacing: 3px;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.mainContent {
    margin: 0;
    justify-content: space-between;
    background-color: white;

}

.navBar {
    display: flex;
    height: 8vh;
    align-items: center;
    justify-content: space-around;
    color: azure;
    font-size: 24px;
    font-weight: bold;
    letter-spacing: 6px;
}
.intro {
    height: 40vh;
    margin: 0 5rem;
}

.greetings {
    display: inline-block;
    overflow: hidden;
    margin: 0 auto;
}

.title {
    font-size: 32px;
    font-weight: bold;
    display: inline;
    letter-spacing: 3px;
}

@keyframes typing {
    from {
        width: 0;
    }
    to {
        width: 60%;
    }
}

/* The typewriter cursor effect */
@keyframes blink-caret {
    from,
    to {
        border-color: transparent;
    }
    50% {
        border-color: orange;
    }
}
.footer {
    color: #fff;
    font-size: 16px;
    height: 15vh;
}

#letstalk {
    padding-left: 30px;
    font-weight: bolder;
    font-size: 46px;
    margin-bottom: 75px;
    display: block;
}

.bg-contact {
    background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.1)), url('./assets/img/bgmobile.jpg');
    background-size: cover;
    color: rgb(0, 0, 0);
}
.btnContact {
    width: 50% !important;
    margin: auto !important;
    background: #343a40 !important;
    padding: 1rem !important;
    border-color: #343a40 !important;
    font-weight: bold !important;
    letter-spacing: 2px !important;
}

form {
    margin-top: 10px;
}

form label {
    font-size: 22px;
    font-weight: bold;
}

